////
/// This is City banner
////

.banner {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background-color: $color-banner-this-is-city;
    color: $color-purewhite;

    &:before{
        box-shadow: inset 0 20px 20px -20px rgba(0, 0, 0, 0.15);
        content: "";
        position: absolute;
        top: 0;
        width: 100%;
        height: $measure--small4;
    }

    @include media('>tablet') {
        @include standard-grid();
        padding: 0;
        grid-column: 1/-1;
        grid-template-rows: repeat(2, 1fr);
        align-items: center;
    }
}

.wrapper--txt-ribbon {
    @include media('>tablet') {
        grid-column: 2/12;
        grid-row: 1/-1;
    }
}

.banner__txt {
    margin-top: $measure--medium3;
    @include media('<=tablet') {
        grid-row: unset;
        grid-column: unset;
        @include default-padding();
    }
    
    @include media('>tablet') {
        margin-bottom: $measure--threequarters;
    }
}

.banner__ribbon {
    margin-bottom: $measure--medium3;
    @include media('<=tablet') {
        grid-row: unset;
        grid-column: unset;
        margin-top: $measure--basic;
        @include default-padding();
    }
}

.banner__img {
    @include media('<=tablet') {
        display: none;
        grid-column: 12 / -1;
    }

    @include media('>tablet') {
        display: block;
        max-height: $banner-tablet-image-max-height;
        grid-row: 1 / span 2;
        grid-column: 12 / -1;
    }

    @include media('>=desktop') {
        max-height: $banner-desktop-image-max-height;
    }

    @include media('>=4K') {
        max-height: $banner-4k-image-max-height;
    }

    @include media('>=8K') {
        max-height: $banner-8k-image-max-height;
    }

    .hero-image {
        width: 100%;
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            object-fit: cover;
            align-self: auto;
        }
    }
}

.banner__img--mobile {
    display: block;
    overflow: hidden;

    @include media('<=mobile') {
        max-height: $banner-mobile-image-max-height;
    }

    @include media('<=tablet') {
        max-height: $banner-tablet-image-max-height;
    }

    @include media('>tablet') {
        display: none;
    }

    .hero-image {
        width: 100%;
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            object-fit: cover;
            align-self: auto;
        }
    }
}

//// Banner - highlight H1 variation

.banner--highlight {
    .banner__txt__padding {
        padding: 0 $measure--half;

        h1,
        p,
        span {
            color: $color-banner-this-is-city;
            background-color: $color-purewhite;
            box-shadow: $measure--half 0 0 $color-purewhite, -$measure--half 0 0 $color-purewhite;
            box-decoration-break: clone;
            line-height: $measure--medium3;
            display: inline;
            
            @include media('>=tablet'){
                line-height: $measure--medium5;
            }
        }
    }
}
